<template>
<div class="col-12 col-sm-6 col-md-4">
  <div class="card-top-level">
    <!-- <div v-if="imageName">
      <img :src="require('../assets/' + imageName)" class="card-image"/>
    </div>
    <div v-else>
      <div class="card-image"></div>
    </div> -->
    <div class="menu-card">
      <div class="title">
        <strong v-if="marked" class="star">&#42;</strong>
        <h5>{{ name }}</h5>
      </div>
      <div class="item-header"></div>
      <p>{{ desc }}</p>
    </div>
    <p class="price"><span v-if="!isNaN(price)">$</span>{{ price }}</p>
  </div>
</div>
</template>

<script>
export default {
  name: 'MenuItem',
  props: ['index', 'name', 'desc', 'price', 'imageName', 'marked']
}
</script>

<style scoped>
.card-top-level {
  display: flex;
  margin-top: 10px;
  margin-right: 20px;
  margin-left: 20px;
  background-color: white;
  max-width: 400px;
  padding: 0;
  min-height: 150px;
  box-shadow: 0px 0px 3px #888888;
}
.menu-card {
  padding: 10px;
}

.card-image {
  height: 150px;
  width: 150px;
}

.price {
  margin: 10px 10px auto auto;
}

.title {
  display: flex;
}

.star {
  color: #8a0707;
  font-size: 1.6em;
  margin-right: 5px;
  margin-top: 3px;
}

.item-header {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  width: 50px;
  border-bottom: 1px solid #8a0707;
}
</style>



